@import url(https://fonts.googleapis.com/css?family=Karla:400,500,700&display=swap);
body,
html {
  margin: 0;
}

.App {
  max-width: 414px;
  width: 100%;
  margin: 0 auto;
  height: 896px;
  overflow-y: scroll;
  background-color: #2c3039;
  position: relative;
}

.App * {
  font-family: "Karla", sans-serif;
}

.App * {
  color: #fff;
}

.timesContainer {
  padding: 0 25px 25px;
  position: relative;
  z-index: 3;
}

ul.trainTimes {
  margin: -130px 0 0;
  list-style: none;
  padding: 0;
}

ul.trainTimes li {
  margin-bottom: 20px;
  -webkit-filter: drop-shadow(0px 1px 5px rgba(0, 0, 0, 0.5));
          filter: drop-shadow(0px 1px 5px rgba(0, 0, 0, 0.5));
}

ul.trainTimes li .cardTop {
  background: rgba(255, 255, 255, 0.15);
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
  padding: 20px;
}

.times {
  margin-bottom: 15px;
}

.times,
.cardTop .location {
  display: flex;
}

.times .departure,
.times .arrival,
.location .start,
.loaction .end,
.location .time {
  flex: 1 1;
}

.times .arrival,
.location .end {
  text-align: right;
}
.times .dots {
  flex: 2 1;
  text-align: center;
}

.location .time {
  text-align: center;
}

.times p {
  margin: 0;
  font-size: 24px;
  color: #fff;
  font-weight: 700;
}

.times .delayed p {
  color: #ff2897;
}

.times .delayed s {
  font-size: 16px;
  opacity: 0.5;
}

.times s.fade {
  opacity: 0;
  pointer-events: none;
}

.location p {
  font-size: 12px;
  margin: 0;
  color: #00ebc8;
}

.location p span {
  color: #cccccc;
  opacity: 0.75;
  font-size: 10px;
}

ul.trainTimes li .cardBottom {
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
  background: #3d3f49;
  padding: 8px 20px;
  font-size: 10px;
}

.cardBottom p {
  margin: 0;
  color: rgba(255, 255, 255, 0.65);
  opacity: 0.8;
}

.cardBottom p span.operator {
  color: #fff;
}

.cardBottom p span.location {
  color: #ff2897;
}

footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 99;
  background: #3b3d46;
  height: 70px;
}

header {
  height: 258px;
  position: relative;
  padding: 40px 25px;
  box-sizing: border-box;
}

header::before,
header::after {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  content: "";
}

header::before {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#3d3f49), to(#9500ed));
  background-image: linear-gradient(180deg, #3d3f49 0%, #9500ed 100%);
  opacity: 0.5;
  z-index: 1;
}

header::after {
  background-image: url(/OnTrack/static/media/bg.856358a4.jpg);
  opacity: 0.1;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center bottom;
  z-index: 2;
}

header .headerLocation,
header .headerCrs {
  display: grid;
  position: relative;
  z-index: 3;
}

header .headerLocation {
  grid-template-columns: 1fr 1fr;
}

header .headerCrs {
  grid-template-columns: auto 1fr auto;
}

header .headerLocation p {
  color: rgba(255, 255, 255, 0.75);
  font-size: 12px;
  margin: 0;
}

header .headerLocation p:last-child,
header .headerCrs h2:last-child {
  text-align: right;
  -webkit-animation-delay: 1.3s;
          animation-delay: 1.3s;
}

header .headerCrs h2 {
  margin: 0;
  font-weight: 700;
  font-size: 32px;
  -webkit-animation-delay: 0.15s;
          animation-delay: 0.15s;
}

header .headerCrs h2:last-child {
  -webkit-animation-delay: 1.45s;
          animation-delay: 1.45s;
}

header .headerCrs .graphicContainer {
  padding: 2px 15px 0;
  display: grid;
  grid-template-columns: auto 1fr auto;
}

header .headerCrs .graphicContainer ul {
  margin: 0;
  list-style: none;
  padding: 0;
  text-align: center;
  padding-top: 3px;
}

header .headerCrs .graphicContainer ul li {
  display: inline-block;
  margin: 0 7px;
  vertical-align: middle;
  border-radius: 100%;
  background: white;
  position: relative;
}

header .headerCrs .graphicContainer ul:first-child li:last-child,
header .headerCrs .graphicContainer ul:last-child li:first-child {
  width: 8px;
  height: 8px;
}

header .headerCrs .graphicContainer ul li:nth-child(2) {
  width: 6px;
  height: 6px;
  opacity: 0.6;
}

header .headerCrs .graphicContainer ul:first-child li:first-child,
header .headerCrs .graphicContainer ul:last-child li:last-child {
  width: 4px;
  height: 4px;
  opacity: 0.4;
}

header .headerCrs .graphicContainer ul:first-child li:first-child {
  -webkit-animation-delay: 0.25s;
          animation-delay: 0.25s;
}

header .headerCrs .graphicContainer ul:first-child li:nth-child(2) {
  -webkit-animation-delay: 0.4s;
          animation-delay: 0.4s;
}

header .headerCrs .graphicContainer ul:first-child li:last-child {
  -webkit-animation-delay: 0.55s;
          animation-delay: 0.55s;
}

header .headerCrs img {
  width: 42px;
  -webkit-animation-delay: 0.7s;
          animation-delay: 0.7s;
  padding: 0 7px;
  margin: 0 auto;
  position: relative;
  top: 6px;
}

header .headerCrs .graphicContainer ul:last-child li:first-child {
  -webkit-animation-delay: 0.85s;
          animation-delay: 0.85s;
}

header .headerCrs .graphicContainer ul:last-child li:nth-child(2) {
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}

header .headerCrs .graphicContainer ul:last-child li:last-child {
  -webkit-animation-delay: 1.15s;
          animation-delay: 1.15s;
}

@media screen and (max-width: 414px) {
  header .headerCrs h2 {
    font-size: 28px;
  }
}

